const DrivingTeam = ({ drivers }) => {
  const [refresh, setRefresh] = useState(false);
  const [min, setMin] = useState(true);
  const [followed, setFollowed] = useState(null);
  const driversTotal = drivers ? Object.keys(drivers).length : 0;
  useEffect(() => {
    if (driversTotal === 0) {
      setMin(true);
      setFollowed(null);
    } else {
      if (driversTotal === 1) {
        const did = Object.keys(drivers);
        global.followed = drivers[did[0]];
        setFollowed(drivers[did[0]]);
        setRefresh((r) => !r);
      }
    }
  }, [driversTotal]);

  return (
    <Card
      titlestyle={{ fontSize: vw(22) }}
      headerStyle={{
        borderBottomWidth: min ? 0 : vw(2),
      }}
      style={{
        width: vw(400),
        // height: vh(460),
        position: "absolute",
        bottom: vh(30),
        right: vw(30),
        paddingHorizontal: vw(10),
        paddingVertical: vh(10),
        zoom: "85%",
      }}
      title={min ? `Drivers Driving (${driversTotal})` : "Your Driving Team"}
      subtitle={followed ? "Following " + followed.name : ""}
      rightElement={
        <Pressable
          onPress={() => {
            setMin((m) => !m);
          }}
          rounded={"full"}
          bg={Colors.primary}
        >
          {min ? (
            <ArrowUpIcon fill={"white"} />
          ) : (
            <ArrowDownIcon fill={"white"} />
          )}
        </Pressable>
      }
    >
      {!min && (
        <VStack marginTop={vh(10)}>
          {drivers &&
            driversTotal > 0 &&
            Object.keys(drivers).map((d, idx) => {
              const driver = drivers[d];
              return (
                <HStack
                  key={"u-" + idx}
                  flex={1}
                  space={2}
                  alignItems={"center"}
                  justifyContent={"flex-start"}
                  style={{
                    borderBottomColor: "rgba(0,0,0,0.15)",
                    borderBottomWidth: drivers.length === idx + 1 ? 0 : vw(0.5),
                  }}
                >
                  <Center
                    style={{
                      marginVertical: vh(16),
                      // marginRight: vh(10),
                    }}
                  >
                    <Avatar style={{ width: vw(36), height: vh(36) }}></Avatar>
                  </Center>
                  <HStack
                    style={{ width: vw(300) }}
                    justifyContent={"space-between"}
                  >
                    <VStack>
                      <Text style={{ fontFamily: "Poppins", fontSize: vw(17) }}>
                        {driver.name.length <= 15
                          ? driver.name
                          : driver.name.substring(0, 15) + "..."}
                      </Text>
                      <Text
                        style={{
                          fontFamily: "Poppins",
                          fontSize: vw(13),
                          color: "#919191",
                          textTransform: "capitalize",
                        }}
                      >
                        {driver.role}
                      </Text>
                    </VStack>
                    <ShadowButton
                      space={3}
                      style={{ height: vh(32), marginTop: vh(5) }}
                      onPress={() => {
                        setFollowed((f) => {
                          // const fwd = f
                          //   ? drivers.length === 1
                          //     ? null
                          //     : driver
                          //   : driver;
                          const fwd =
                            f && f.user === driver.user ? null : driver;
                          global.followed = fwd;
                          return fwd;
                        });
                        setRefresh((r) => !r);
                      }}
                      label={
                        followed && followed.user === driver.user
                          ? "UNFOLLOW"
                          : "FOLLOW"
                      }
                    />
                  </HStack>
                </HStack>
              );
            })}
        </VStack>
      )}
    </Card>
  );
};

