function createPolyLine() {
  //create a symbol to animate along the route
  var lineSymbol = {
    path: window.google.maps.SymbolPath.CIRCLE,
    scale: 8,
    fillColor: "#566895",
    fillOpacity: 1,
    strokeColor: "#282c41",
    strokeOpacity: 1,
    strokeWeight: 2,
  };
  global.center = lineCoordinates[0];

  //create a polyline
  polyline = new window.google.maps.Polyline({
    path: lineCoordinates,
    strokeColor: "#f39e9e",
    strokeWeight: 5,
    icons: [
      {
        icon: lineSymbol,
        offset: "100%",
      },
    ],
    map: global.map,
  });
}

function animatePolyline() {
  var i = 0;
  var pause = false;
  var pauseLineRemove = 1500;
  var pauseRedrawLine = 1000;

  //experiment with the speed based on the total parts in the line
  var drawSpeed = 50;

  setInterval(function () {
    //check if the end of the array is reached
    if (i + 1 == lineCoordinates.length && !pause) {
      pause = true;

      //remove all the line parts, optionally with a delay to keep the fully drawn line on the map for a while
      // setTimeout(function () {
      //   for (var j = 0; j < linePartArr.length; j++) {
      //     linePartArr[j].setMap(null);
      //   }

      //   linePartArr = [];
      // }, pauseLineRemove);

      //delay the drawing of the next animated line
      // setTimeout(function () {
      //   pause = false;
      //   i = 0;
      // }, pauseRedrawLine + pauseLineRemove);
    }

    //create a line part between the current and next coordinate
    if (!pause) {
      var part = [];
      part.push(lineCoordinates[i]);
      part.push(lineCoordinates[i + 1]);

      //create a polyline
      var linePart = new window.google.maps.Polyline({
        path: part,
        strokeColor: "#ff0000",
        strokeOpacity: 1,
        strokeWeight: 5,
        zIndex: i + 2,
        map: global.map,
      });

      //add the polyline to an array
      linePartArr.push(linePart);

      i++;
    }
  }, drawSpeed);
}

//animate the icon on the map
function animateIcon() {
  var lineOffset = 0;

  //experiment with the speed based on the length of the line
  var iconSpeed = 0.2;

  //move the icon
  setInterval(function () {
    lineOffset = (lineOffset + iconSpeed) % 200;
    var lineIcon = polyline.get("icons");
    lineIcon[0].offset = lineOffset / 2 + "%";
    polyline.set("icons", lineIcon);
  }, 20);
}

const Map = () => {
  const dispatch = useDispatch();
  const car = useRef();
  const pRef = useRef();
  const user = getUser();
  const [drivers, setDrivers] = useState([]);
  const [refresh, setRefresh] = useState(false);
  const [socket, setSocket] = useState();
  const [points, setPoints] = useState();
  const mh = 100 - parseFloat(vh(70)) + "vh";
  // const { profile } = useSelector((state) => state.auth);

  const containerStyle = {
    width: "100%",
    height: mh,
    cursor: "pointer",
    overFlow: "hidden",
    // flex:1,
    // position:"absolute"
  };

  const center = {
    lat: 39.71524829932301,
    lng: -101.2114633395384,
  };

  const options = {
    strokeColor: "#FF0000",
    strokeOpacity: 0.8,
    strokeWeight: 2,
    fillColor: "#FF0000",
    fillOpacity: 0.35,
    clickable: false,
    draggable: false,
    editable: false,
    visible: true,
    radius: 30000,
    zIndex: 1,
  };

  const { isLoaded } = useJsApiLoader({
    id: "google-map-script",
    googleMapsApiKey: "AIzaSyDmwZdL8fB-zBbuBfm6cXl_IgaMvkH-KLE",
    libraries,
  });
  const [map, setMap] = React.useState(null);
  const [mapOptions] = useState({
    gestureHandling: "auto",
    minZoom: 3,
    maxZoom: 17,
    fullscreenControl: false,
    mapTypeControl: false,
    rotateControl: false,
    zoomControl: false,
    streetViewControl: false,
  });

  const connectSocket = async () => {
    // const s1 = `https://drive.shadow.properties`;
    const s1 = `ws://localhost:8080`;
    const socket = io(s1, {
      // reconnection: true,
      transports: ["websocket"],
      // allowUpgrades: true,
      // pingTimeout: 30000,
    });

    socket
      .on("connect", () => {
        console.log("connected to ", s1, socket.id);
        socket.emit("web:join", {
          name: user.name,
          owner: user.owner,
          user: user._id,
          team: user.team,
          web: true,
        });
      })
      .on("reconnect", async (attempts) => {
        console.log("reconnected to drive server");
      })
      .on("disconnect", async () => {
        console.log("disconnected from drive server");
        delete global.map;
        delete global.center;
        delete global.drivers;
        setDrivers(null);
      })
      .on("error", (error) => {
        console.log("drive server error", error);
      });

    setSocket(socket);
  };

  useEffect(() => {
    if (socket) {
      if (drivers.length === 0) {
        socket.emit("web:team:drivers");
      }

      global.drivers = {};
      global.activeDrivers = 0;
      var startPos = [42.42679066670903, -83.29210638999939];
      var speed = 100; // km/h

      var delay = 50;

      socket
        .on("team:drivers", async (drivers) => {
          console.log(drivers);
          let activeDrivers =
            drivers && drivers.length > 0
              ? drivers.filter((d) => {
                  return d.team === user.team && d.driving;
                })
              : [];

          // console.log("team:drivers", activeDrivers);
          if (activeDrivers.length === 0) {
            global.drivers &&
              Object.keys(global.drivers).map((d) => {
                if (global.drivers[d].marker) {
                  global.drivers[d].marker.setMap(null);
                }
                if (global.drivers[d].polyline) {
                  global.drivers[d].polyline.setMap(null);
                }
              });
            global.drivers = {};
            setDrivers(global.drivers);
          } else {
            activeDrivers = activeDrivers.map((a) => {
              const { latitude, longitude } = a.point.location;
              const latlng = {
                lat: latitude,
                lng: longitude,
              };

              if (!global.drivers[a.user]) {
                const polyline = new window.google.maps.Polyline({
                  path: [latlng],
                  geodesic: true,
                  strokeColor: a.color || "red",
                  strokeOpacity: 1.0,
                  strokeWeight: 2,
                  map: global.map,
                  // icons:[ {
                  //   path: window.google.maps.SymbolPath.CIRCLE,
                  //   scale: 7,
                  //   strokeColor: a.color || "red",
                  // }]
                });

                const marker = new window.google.maps.Marker({
                  position: latlng,
                  map: global.map,
                  title: a.name,
                  icon: {
                    path: window.google.maps.SymbolPath.CIRCLE,
                    scale: 7,
                    strokeColor: a.color || "red",
                    fillColor: a.color,
                    fillOpacity: 0.8,
                  },
                });
                global.drivers[a.user] = { ...a, marker, polyline };
                // console.log("Center", latlng);
                // map.setCenter(latlng);
                // global.map.setCenter(latlng);
                setDrivers(global.drivers);
              }
              return a;
            });
            global.activeDrivers = activeDrivers.length;

            console.log("global.activeDrivers", global.activeDrivers);
          }
          setRefresh((r) => !r);
        })
        .on("followed:user", (data) => {
          // console.log("LiveRoutes Followed User", data);
          if (
            data &&
            !global.gettingfolloweduser &&
            global.fu &&
            global.followedUser &&
            global.followedUser.user
          ) {
            const { latitude, longitude, heading, user } = data;

            if (
              global.followedUser.user === user &&
              map.current &&
              global.isTouched === 0
            ) {
              global.fu =
                global.fu && global.fu.length > 0
                  ? [...global.fu, { latitude, longitude }]
                  : [{ latitude, longitude }];
              if (!global.resetPointer) {
                // cd.setValue({
                //   latitude,
                //   longitude,
                //   latitudeDelta: 0.00038604507560672765,
                //   longitudeDelta: 0.0006866455078124996,
                // });
                // cd.timing({ latitude, longitude }, { duration: 0 }).start();
                global.resetPointer = true;
              }

              if (!global.ot) {
                global.ot = 0;
              }

              // car.current &&
              //   car.current.setNativeProps({
              //     rotation: data.heading,
              //   });
              // console.log(
              //   "global.fu && global.fu.length",
              //   global.fu && global.fu.length
              // );
              // console.log("Followed User", latitude, longitude);
              // global.fu &&
              //   global.fu.length &&
              // trail.current &&
              // trail.current.setNativeProps({
              //   coordinates: global.fu,
              // });

              if (global.ot % 10) {
                // cd.timing({ latitude, longitude }, { duration: 1000 }).start();
              }

              if (!global.animated) {
                global.animated = true;
                // map.current &&
                //   map.current.animateCamera(
                //     {
                //       center: { latitude, longitude },
                //       heading,
                //       zoom: 17,
                //     },
                //     { duration: 1000 }
                //   );
              }
            }
            global.ot++;
          }
        })
        .on("driver:disconnected", (user) => {
          if (global.drivers && global.drivers[user]) {
            console.log("driver:disconnected", global.drivers[user].name);
            const driver = global.drivers[user];
            driver.marker.setMap(null);
            driver.polyline.setMap(null);
            delete global.drivers[user];
            console.log("active drivers:", global.drivers.length);
          }
        })
        .on("drive:position", async (data) => {
          if (global.drivers && global.drivers[data.user]) {
            const driver = global.drivers[data.user];
            const latlng = new window.google.maps.LatLng(
              data.latitude,
              data.longitude
            );
            if (!global.center) {
              global.map.setCenter(latlng);
              global.center = latlng;
            }

            if (global.map) {
              if (!driver.polyline.map) {
                driver.marker.setMap(global.map);
                driver.polyline.setMap(global.map);
              }
              const from = driver.marker.getPosition();
              const to = latlng;
              // animateM(arker(driver.marker, coords, driver.speed);
              console.log(latlng.lat());
              // await sleep(1000)
              animatedMove(
                driver.marker,
                driver.polyline,
                from,
                to,
                driver.user
              );
              // driver.marker.setPosition(latlng);
              // driver.polyline.getPath().push(latlng);
              // global.map.panTo(latlng);

              // if (global.followed && global.followed.user === driver.user) {
              //   global.map.setCenter(latlng);
              // }
            }
          }
        });
    }
    return () => {
      if (socket) {
        // console.log("live page off");
        // socket.off("team:drivers");
        // socket.off("followed:user");
      }
    };
  }, [socket]);

  const onLoad = React.useCallback(function callback(map) {
    global.map = map;
    setMap(map);
    connectSocket();
    // createPolyLine();
    // animatePolyline();
    // animateIcon();
  }, []);

  const onUnmount = React.useCallback(function callback(map) {
    delete global.map;
    delete global.center;
    delete global.drivers;
    setMap(null);
    setDrivers([]);
    if (socket) {
      console.log("Disconnected");
      socket.disconnect();
    }
  }, []);

  const animatedMove = (marker, polyline, from, to, user) => {
    var deltalat = (to.lat() - from.lat()) / 100;
    var deltalng = (to.lng() - from.lng()) / 100;

    for (var i = 0; i < 100; i++) {
      (function (ind) {
        setTimeout(function () {
          var lat = marker.getPosition().lat();
          var lng = marker.getPosition().lng();

          lat += deltalat;
          lng += deltalng;
          const latlng = new window.google.maps.LatLng(lat, lng);
          marker.setPosition(latlng);
          polyline.getPath().push(latlng);
          if (global.followed && global.followed.user === user) {
            global.map.setCenter(latlng);
          }
          // map.setCenter(latlng);
        }, 10 * ind);
      })(i);
    }
  };


  return isLoaded ? (
    <GoogleMap
      mapContainerStyle={containerStyle}
      center={global.center || center}
      zoom={17}
      minZoom={3}
      maxZoom={17}
      onLoad={onLoad}
      onUnmount={onUnmount}
      mapTypeId={"roadmap"}
      options={mapOptions}
      onIdle={() => {
        // console.log(map.getZoom());
      }}
    >
      <DrivingTeam drivers={drivers} />
    </GoogleMap>
  ) : (
    <></>
  );
};

const Index = ({ user, setLogout }) => {
  return (
    <VStack flex={1}>
      <NavBar user={user} setLogout={setLogout} />
      {/* Map Block */}
      <Map user={user} />
    </VStack>
  );
};

export default Index;
